.button-box{
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(5, 1fr);
  justify-items: stretch;
  align-items: stretch;
  row-gap: 10px;
  column-gap: 10px;
  height: 62.5vh;
  padding-bottom: 20px;
  padding: 0 10px;
  padding-bottom: 20px;
}

.spanned{
  grid-column: 4;
  grid-row: 4 /span 2;
}