* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.main-frame {
  width: 100vw;
  height: 100vh;
}

.main-frame-light{
  background-color: #ffdd5e;
}

.main-frame-dark{
  background-color: #2b2b2b;
}

.title {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 15px 0;
  height: 12.5vh;
}

.title-dark{
  color: #b7e9d1;
}

.title-light{
  color: #222222;
}