.btn-box {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 7px;
}

.btn {
  width: 100%;
  height: 100%;
  border-radius: 500px;
  border: none;
  font-weight: bold;
  font-size: 1.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.btn:active {
  opacity: 0.35;
}

.btn:focus {
  outline: none;
}

.btn-light {
  background-color: #ffd440;
  background-image:radial-gradient(#ffda5b,#ffd440);
  color: #222222;
  box-shadow: 1px 1px 6px rgb(167,129,0);
}

.btn-dark {
  background-color: #666666;
  background-image:radial-gradient(#787878,#666666);
  color: rgb(235,235,235);
  box-shadow: 1px 1px 6px black;
}