.screen{
  height: 23vh;
  margin-bottom: 2vh;
  margin: 5px;
  border-radius: 15px;
  padding: 15px;
  display: flex;
  flex-direction: column;
  text-align: right;
  justify-content: flex-end;
}

.ans, .exp{
  border: none;
  text-align: right;
}

.ans{
  margin-top: 10px;
  overflow-x: scroll;
  font-size: 3rem;
}

.exp{
  font-size: 1.15rem;
}

.sc-light{
  background-color: #fffaec;
}

.sc-dark {
  background-color: #3b3b3b;
}

.exp-light{
  background-color: #fffaec;
  color: #616161;
  border-bottom: 1px solid #616161;
}

.ans-light{
  background-color: #fffaec;
  color: #222222;
}

.exp-dark{
  background-color: #3b3b3b;
  color: #baedd5;
  border-bottom: 1px solid #baedd5;
}

.ans-dark{
  background-color: #3b3b3b;
  color: #baedd5;
}